.form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  box-sizing: border-box;
  border: 0;
  margin-bottom: 16px;

  & textarea {
    min-height: 148px;
    resize: none;
  }

  &__input {
    border: none;
    outline: none;
    width: 100%;
    height: 48px;
    box-sizing: border-box;
    border-radius: 8px;
    padding: 14px 16px;
    background-color: #f9f9f9;

    &::placeholder {
      color: #7e7e83;
    }

    &:hover {
      color: $main-text-color;
      border: 1px solid #dbdbdb;
      padding-left: 15px;
    }

    &:active {
      color: $main-text-color;
      border: 2px solid $button-color;
      padding-left: 14px;
    }
  }

  &__button {
    margin-top: 32px;
  }
}

textarea, input {
  border: none;
  outline: none;
  width: 100%;
  background-color: #f9f9f9;
  resize: none;
  font-family: Manrope, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 19.6px;
  letter-spacing: 0.15px;
}
